.section--intro {
  min-height: calc(100vh - 136px);
  padding: 40px 0 80px;
  overflow: hidden;

  @media(min-width: 992px) {
    padding: 60px 0 195px;
  }

  .container {
    position: relative;
  }

  .decoration {
    display: none;

    @media(min-width: 992px) {
      display: block;
      position: absolute;
      top: -60px;
      right: -79px;
      width: 518px;
      height: 1312px;
    }
  }
}

.page-title {
  margin-bottom: 36px;
}

.page-subtitle {
  width: 423px;
  margin-bottom: 116px;
  font-weight: 400;
}

.decoration__image {
  display: block;
  width: 100%;
  height: 100%;
}

.essay-list__item {
  display: block;

  & + & {
    margin-top: 20px;
  }
}

.essay-list__link {
  display: flex;
  align-items: flex-start;
  text-decoration: none;

  &:hover {
    .essay-list__body {
      color: #368CBF;
    }
  }
}

.essay-list__icon {
  flex-shrink: 0;
  width: 16px;
  height: 17px;
  margin-right: 38px;

  svg {
    display: block;
    width: 100%;
    height: 100%;
  }
}

.essay-list {
  margin-bottom: 45px;
}

.essay-list__body {
  max-width: 596px;
}

.pagination {
  display: flex;
  align-items: center;
}

.pagination__link {
  display: block;
  height: 100%;
  font-size: inherit;
  line-height: inherit;
  text-decoration: none;
  border-bottom: none;
}

.pagination__item {
  & + & {
    margin-left: 10px;
  }

  .pagination__item--prev + & {
    margin-left: 13px;
  }
}

.pagination__item--prev,
.pagination__item--next {
  width: 22px;
  height: 8px;
}

.pagination__item--next {
  transform: rotate(180deg);
}